<template>
  <div class="baidu-map-peripheral-wrapper">
    <div class="search-wrapper mgb-20">
      <el-input class='search-item' placeholder="请输入查询的地名" size='small' v-model="address" @keyup.enter.native="handleSearch">
        <template slot="append">
          <el-button size='small' type='primary' @click.native='handleSearch'>search</el-button>
        </template>
      </el-input>
    </div>
    <div class="peripheral-buttons-wrapper mgb-20">
      <span
        class="peripheral-buttons"
        v-for="i in peripheralButtons"
        :key="i"
        >{{ i }}</span
      >
    </div>
    <div class="baidu-map-information-wrapper">
      <div class="desc-informations mgr-20"></div>
      <div class="baidu-map">
        <baidu-map
          class="map-box"
          :center="center"
          @ready="mapReady"
          :zoom="15"
          :min-zoom="13"
          :scroll-wheel-zoom="true"
        >
          <bm-overlay
            ref="customOverlay"
            :class="{ sample: true }"
            pane="labelPane"
            @draw="draw"
          >
            <div class="circle"></div>
          </bm-overlay>
        </baidu-map>
      </div>
    </div>
  </div>
</template>

<script>
import { $jsonp } from '@/assets/js/utils';
export default {
  name: 'BaiduMapPeripheral',
  components: {},
  props: {},
  data () {
    return {
      city: '深圳', // 默认查询深圳市
      address: '深大',
      keyword: '地铁',
      center: {
        lng: 113.95008534684693,
        lat: 22.543237423924563
      },
      BMAP: null,
      MAP: null
    };
  },
  computed: {
    peripheralButtons () {
      return [
        '交通',
        '商超',
        '美食',
        '咖啡厅',
        '酒吧',
        '公园',
        '景点',
        '医院',
        '药店'
      ];
    }
  },
  methods: {
    // 百度地图根据详细地址信息获取坐标
    getAssignAddressDetails () {
      $jsonp({
        url: 'http://api.map.baidu.com/geocoder/v2/',
        data: {
          address: this.address,
          city: '深圳', // 默认查询深圳
          output: 'json',
          scope: 2,
          ak: 'jEVOBuDco4uCCkoDytYgVLMuTzAPHWUI'
        },
        callback: 'callback',
        success: data => {
          if (data.status !== 0) {
            this.$message.warning('请输入适合的地点名称');
          } else {
            const lat = data.result.location.lat;
            const lng = data.result.location.lng;
            this.getAssignPointDetails(lat, lng).then((data) => {
              console.log(data);
            });
            this.center = {
              lng, lat
            };
          }
        },
        fail: err => {
          console.log(err);
        }
      });
    },
    // 百度地图根据坐标获取详细地址信息
    getAssignPointDetails (lat, lng) {
      return new Promise((resolve) => {
        $jsonp({
          url: 'http://api.map.baidu.com/geocoder/v2/',
          data: {
            location: `${lat},${lng}`, // 经度,纬度
            output: 'json',
            scope: 2,
            ak: 'jEVOBuDco4uCCkoDytYgVLMuTzAPHWUI'
          },
          callback: 'callback',
          success: data => {
            resolve(data);
          },
          fail: err => {
            console.log(err);
          }
        });
      });
    },
    handleSearch () {
      const address = this.address;
      const BMap = this.BMAP;
      const Geocoder = new BMap.Geocoder();
      Geocoder.getPoint(address, point => {
        this.center = point;
        this.mapLocalSearch(res => {
          console.log(res);
        });
      }, '深圳市');
      // this.getAssignAddressDetails();
    },
    mapReady ({ BMap, map }) {
      this.BMAP = BMap;
      this.MAP = map;
    },
    draw ({ el, BMap, map }) {
      const { lng, lat } = this.center;
      const pixel = map.pointToOverlayPixel(new BMap.Point(lng, lat));
      el.style.left = pixel.x - 10 + 'px';
      el.style.top = pixel.y - 10 + 'px';
    },
    // 搜索指定周边，并获取相关数据
    mapLocalSearch (cb) {
      const { lng, lat } = this.center;
      const Point = new this.BMAP.Point(lng, lat);
      let localSearch = new this.BMAP.LocalSearch(Point, {
        onSearchComplete: cb
      });

      localSearch.searchNearby(this.keyword, Point, 2000);
    },
  },
  created () {},
  mounted () {},
  watch: {}
};
</script>

